<template>
  <div class="main">
    <div>
      <router-view></router-view>

    </div>
    
    <van-tabbar v-model="index"  @change="toggleTabBar">
      <van-tabbar-item v-for="(item, index) in navList" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        index: 0,
        
        navList: [
          {
            icon: 'coupon-o',
            title: '菜单',
            router: 'Menu',
            url: '/main/menu'
          },
          {
            icon: 'orders-o',
            title: '订单',
            router: 'Order',
            url: '/main/order'
          },
          {
            icon: 'shopping-cart-o',
            title: '购物车',
            router: 'Shopcart',
            url: '/main/shopcart'
          },
          {
            icon: 'contact',
            title: '我的',
            router: 'My',
            url: '/main/my'
          }
        ]
      };
    },

    created() {
      //获取路径的hash
      let hash = location.hash.slice(1);
      

      //根据路径激活相应路由
      for (let i = 0; i < this.navList.length; i++) {
        if (this.navList[i].url == hash) {
          this.index = i;
          break;
        }
      }
    },

    methods: {

      //切换底部导航
      toggleTabBar(index) {
        
        this.$router.push({name: this.navList[index].router});
      }
    }
  }
</script>

